import React from 'react';
import {
  ListGroup,
  ListGroupItem,
  Row,
  Col,
  Form,
  FormInput,
  FormGroup,
  FormCheckbox,
  FormSelect,
  Button,
} from 'shards-react';
const soapRequest = require ('easy-soap-request');
class CompleteFormExampleLokasi extends React.Component {
  constructor (props) {
    super (props);
    this.state = {
      KODE : "",
      NAMA_LOKASI : "",
      STATUS : "Y"
    }
    this.handleChangeKODE = this.handleChangeKODE.bind(this);
    this.handleChangeNAMA_LOKASI = this.handleChangeNAMA_LOKASI.bind(this);
    this.handleChangeSTATUS = this.handleChangeSTATUS.bind(this);
  }
  async submit() {
    await console.log(this.state);
    const headers = {
      username: 'weblogic',
      password: 'welcome1',
      'Content-Type': 'text/xml;charset=UTF-8',
      soapAction: 'start',
    };
    const xml = `<soap:Envelope xmlns:soap="http://schemas.xmlsoap.org/soap/envelope/">
        <soap:Body>
              <ns1:start xmlns:ns1="http://xmlns.oracle.com/bpmn/bpmnProcess/AddDataLokasi">
                    <KODE>${this.state.KODE}</KODE>
                    <NAMA_LOKASI>${this.state.NAMA_LOKASI}</NAMA_LOKASI>
                    <STATUS>${this.state.STATUS}</STATUS>
          </ns1:start>
      </soap:Body>
    </soap:Envelope>`;
    const {response} = await soapRequest (
      'http://localhost:7101/soa-infra/services/default/BPM_2_MasterLokasi/AddDataLokasi.service?WSDL',
      headers,
      xml,
      1000000
    );
    await console.log(response)
  }
  handleChangeKODE(event) {
    this.setState({KODE: event.target.value});
  }
  handleChangeNAMA_LOKASI(event) {
    this.setState({NAMA_LOKASI: event.target.value});
  }
  handleChangeSTATUS(event) {
    this.setState({STATUS: event.target.value});
  }
  render () {
    return (
      <ListGroup flush>
        <ListGroupItem className="p-3">
          <Row>
            <Col>
              <Form>
                <Row form>
                  <Col md="6" className="form-group">
                    <label htmlFor="feKode">KODE</label>
                    <FormInput onChange={this.handleChangeKODE} value={this.state.KODE} id="feKode" />
                  </Col>
                  <Col md="6">
                    <label htmlFor="feNamaLOKASI">NAMA LOKASI</label>
                    <FormInput onChange={this.handleChangeNAMA_LOKASI} value={this.state.NAMA_LOKASI}  id="feNamaLOKASI" />
                  </Col>
                </Row>
                <FormGroup>
                  <label htmlFor="feStatus">STATUS</label>
                  <FormSelect value={this.state.STATUS} onChange={this.handleChangeSTATUS}  id="feStatus">
                    <option value="Y">Y</option>
                    <option value="N">N</option>
                  </FormSelect>
                </FormGroup>
                <Button onClick={async () => await this.submit()}>SUBMIT DATA</Button>
              </Form>
            </Col>
          </Row>
        </ListGroupItem>
      </ListGroup>
    );
  }
}

export default CompleteFormExampleLokasi;


