import React from 'react';
import {
  ListGroup,
  ListGroupItem,
  Row,
  Col,
  Form,
  FormInput,
  FormGroup,
  FormCheckbox,
  FormSelect,
  Button,
} from 'shards-react';
const soapRequest = require ('easy-soap-request');
class CompleteFormExampleMasterPICRumahSakit extends React.Component {
  constructor (props) {
    super (props);
    this.state = {
      NPP : "",
      NAMA : "",
      DESKRIPSI : "",
      EMAIL : "",
      RS : ""
    }
    this.handleChangeNPP = this.handleChangeNPP.bind(this);
    this.handleChangeNAMA = this.handleChangeNAMA.bind(this);
    this.handleChangeDESKRIPSI = this.handleChangeDESKRIPSI.bind(this);
    this.handleChangeEMAIL = this.handleChangeEMAIL.bind(this);
    this.handleChangeRS = this.handleChangeRS.bind(this);
  }
  async submit() {
    await console.log(this.state);
    const headers = {
      username: 'weblogic',
      password: 'welcome1',
      'Content-Type': 'text/xml;charset=UTF-8',
      soapAction: 'start',
    };
    const xml = `<soap:Envelope xmlns:soap="http://schemas.xmlsoap.org/soap/envelope/">
    <soap:Body>
          <ns1:start xmlns:ns1="http://xmlns.oracle.com/bpmn/bpmnProcess/AddPICRumahSakit">
                <NPP>${this.state.NPP}</NPP>
                <NAMA>${this.state.NAMA}</NAMA>
                <DESKRIPSI>${this.state.DESKRIPSI}</DESKRIPSI>
                <EMAIL>${this.state.EMAIL}</EMAIL>
                <RS>${this.state.RS}</RS>
      </ns1:start>
  </soap:Body>
</soap:Envelope>`;
    const {response} = await soapRequest (
      'http://localhost:7101/soa-infra/services/default/BPM_3_MasterPICRumahSakit/AddPICRumahSakit.service?WSDL',
      headers,
      xml,
      1000000
    );
    await console.log(response)
  }
  handleChangeNPP(event) {
    this.setState({NPP: event.target.value});
  }
  handleChangeNAMA(event) {
    this.setState({NAMA: event.target.value});
  }
  handleChangeDESKRIPSI(event) {
    this.setState({DESKRIPSI: event.target.value});
  }
  handleChangeEMAIL(event) {
    this.setState({EMAIL: event.target.value});
  }
  handleChangeRS(event) {
    this.setState({RS: event.target.value});
  }
  render () {
    return (
      <ListGroup flush>
        <ListGroupItem className="p-3">
          <Row>
            <Col>
              <Form>
                <Row form>
                  <Col md="6" className="form-group">
                    <label htmlFor="feIdBank">KODE PIC RUMAH SAKIT</label>
                    <FormInput onChange={this.handleChangeNPP} value={this.state.NPP} id="feIdBank" />
                  </Col>
                  <Col md="6">
                    <label htmlFor="feNamaBank">NAMA PIC RUMAH SAKIT</label>
                    <FormInput onChange={this.handleChangeNAMA} value={this.state.NAMA}  id="feNamaBank" />
                  </Col>
                </Row>

                <FormGroup>
                  <label htmlFor="feDeskripsi">DESKRIPSI</label>
                  <FormInput onChange={this.handleChangeDESKRIPSI} value={this.state.JUMLAH_REKENING}  id="feDeskripsi"/>
                </FormGroup>

                <FormGroup>
                  <label htmlFor="feEmail">EMAIL</label>
                  <FormInput onChange={this.handleChangeEMAIL} value={this.state.EMAIL}  id="feEmail"/>
                </FormGroup>

                <FormGroup>
                  <label htmlFor="feRS">RS</label>
                  <FormInput onChange={this.handleChangeRS} value={this.state.RS}  id="feRS"/>
                </FormGroup>

                <Button onClick={async () => await this.submit()}>SUBMIT DATA</Button>
              </Form>
            </Col>
          </Row>
        </ListGroupItem>
      </ListGroup>
    );
  }
}

export default CompleteFormExampleMasterPICRumahSakit;

