import React from 'react';
import {
  ListGroup,
  ListGroupItem,
  Row,
  Col,
  Form,
  FormInput,
  FormGroup,
  FormCheckbox,
  FormSelect,
  Button,
} from 'shards-react';
const soapRequest = require ('easy-soap-request');
class CompleteFormExampleMasterRumahSakit extends React.Component {
  constructor (props) {
    super (props);
    this.state = {
      KODE_RS : "",
      NAMA_RUMAH_SAKIT : "",
      JENIS : "PEMERINTAH",
      STATUS : "Y"
    }
    this.handleChangeKODE_RS = this.handleChangeKODE_RS.bind(this);
    this.handleChangeNAMA_RUMAH_SAKIT = this.handleChangeNAMA_RUMAH_SAKIT.bind(this);
    this.handleChangeJENIS = this.handleChangeJENIS.bind(this);
    this.handleChangeSTATUS = this.handleChangeSTATUS.bind(this);
  }
  async submit() {
    await console.log(this.state);
    const headers = {
      username: 'weblogic',
      password: 'welcome1',
      'Content-Type': 'text/xml;charset=UTF-8',
      soapAction: 'start',
    };
    const xml = `<soap:Envelope xmlns:soap="http://schemas.xmlsoap.org/soap/envelope/">
    <soap:Body>
          <ns1:start xmlns:ns1="http://xmlns.oracle.com/bpmn/bpmnProcess/AddRumahSakit">
                <KODE_RS>${this.state.KODE_RS}</KODE_RS>
                <NAMA_RUMAH_SAKIT>${this.state.NAMA_RUMAH_SAKIT}</NAMA_RUMAH_SAKIT>
                <JENIS>${this.state.JENIS}</JENIS>
                <STATUS>${this.state.STATUS}</STATUS>
      </ns1:start>
  </soap:Body>
</soap:Envelope>`;
    const {response} = await soapRequest (
      'http://localhost:7101/soa-infra/services/default/BPM_4_MasterRumahSakit/AddRumahSakit.service?WSDL',
      headers,
      xml,
      1000000
    );
    await console.log(response)
  }
  handleChangeKODE_RS(event) {
    this.setState({KODE_RS: event.target.value});
  }
  handleChangeNAMA_RUMAH_SAKIT(event) {
    this.setState({NAMA_RUMAH_SAKIT: event.target.value});
  }
  handleChangeJENIS(event) {
    this.setState({JENIS: event.target.value});
  }
  handleChangeSTATUS(event) {
    this.setState({STATUS: event.target.value});
  }
  render () {
    return (
      <ListGroup flush>
        <ListGroupItem className="p-3">
          <Row>
            <Col>
              <Form>
                <Row form>
                  <Col md="6" className="form-group">
                    <label htmlFor="feIdBank">KODE RUMAH SAKIT</label>
                    <FormInput onChange={this.handleChangeKODE_RS} value={this.state.KODE_RS} id="feIdBank" />
                  </Col>
                  <Col md="6">
                    <label htmlFor="feNamaBank">NAMA RUMAH SAKIT</label>
                    <FormInput onChange={this.handleChangeNAMA_RUMAH_SAKIT} value={this.state.NAMA_RUMAH_SAKIT}  id="feNamaBank" />
                  </Col>
                </Row>

                <FormGroup>
                  <label htmlFor="feJENIS">JENIS</label>
                  <FormSelect value={this.state.JENIS} onChange={this.handleChangeJENIS}  id="feJENIS">
                    <option value="PEMERINTAH">PEMERINTAH</option>
                    <option value="SWASTA">SWASTA</option>
                  </FormSelect>
                </FormGroup>

                <FormGroup>
                  <label htmlFor="feStatus">STATUS</label>
                  <FormSelect value={this.state.STATUS} onChange={this.handleChangeSTATUS}  id="feStatus">
                    <option value="Y">Y</option>
                    <option value="N">N</option>
                  </FormSelect>
                </FormGroup>
                <Button onClick={async () => await this.submit()}>SUBMIT DATA</Button>
              </Form>
            </Col>
          </Row>
        </ListGroupItem>
      </ListGroup>
    );
  }
}

export default CompleteFormExampleMasterRumahSakit;

