import React from 'react';
import {
  ListGroup,
  ListGroupItem,
  Row,
  Col,
  Form,
  FormInput,
  FormGroup,
  FormCheckbox,
  FormSelect,
  Button,
} from 'shards-react';
const soapRequest = require ('easy-soap-request');
class CompleteFormExampleMasterBank extends React.Component {
  constructor (props) {
    super (props);
    this.state = {
      ID_BANK : "",
      NAMA_BANK : "",
      JUMLAH_REKENING : 0,
      KODE_BANK : "",
      STATUS : "AKTIF"
    }
    this.handleChangeID_BANK = this.handleChangeID_BANK.bind(this);
    this.handleChangeNAMA_BANK = this.handleChangeNAMA_BANK.bind(this);
    this.handleChangeJUMLAH_REKENING = this.handleChangeJUMLAH_REKENING.bind(this);
    this.handleChangeKODE_BANK = this.handleChangeKODE_BANK.bind(this);
    this.handleChangeSTATUS = this.handleChangeSTATUS.bind(this);
  }
  async submit() {
    await console.log(this.state);
    const headers = {
      username: 'weblogic',
      password: 'welcome1',
      'Content-Type': 'text/xml;charset=UTF-8',
      soapAction: 'start',
    };
    const xml = `<soap:Envelope xmlns:soap="http://schemas.xmlsoap.org/soap/envelope/">
        <soap:Body>
              <ns1:start xmlns:ns1="http://xmlns.oracle.com/bpmn/bpmnProcess/Process">
                    <ID_BANK>${this.state.ID_BANK}</ID_BANK>
                    <NAMA_BANK>${this.state.NAMA_BANK}</NAMA_BANK>
                    <JUMLAH_REKENING>${this.state.JUMLAH_REKENING}</JUMLAH_REKENING>
                    <KODE_BANK>${this.state.KODE_BANK}</KODE_BANK>
                    <STATUS>${this.state.STATUS}</STATUS>
          </ns1:start>

      </soap:Body>
    </soap:Envelope>
    `;
    const {response} = await soapRequest (
      'http://localhost:7101/soa-infra/services/default/BPM_1_MasterBank/Process.service?WSDL',
      headers,
      xml,
      1000000
    );
    await console.log(response)
  }
  handleChangeID_BANK(event) {
    this.setState({ID_BANK: event.target.value});
  }
  handleChangeNAMA_BANK(event) {
    this.setState({NAMA_BANK: event.target.value});
  }
  handleChangeJUMLAH_REKENING(event) {
    this.setState({JUMLAH_REKENING: event.target.value});
  }
  handleChangeKODE_BANK(event) {
    this.setState({KODE_BANK: event.target.value});
  }
  handleChangeSTATUS(event) {
    this.setState({STATUS: event.target.value});
  }
  render () {
    return (
      <ListGroup flush>
        <ListGroupItem className="p-3">
          <Row>
            <Col>
              <Form>
                <Row form>
                  <Col md="6" className="form-group">
                    <label htmlFor="feIdBank">ID BANK</label>
                    <FormInput onChange={this.handleChangeID_BANK} value={this.state.ID_BANK} id="feIdBank" />
                  </Col>
                  <Col md="6">
                    <label htmlFor="feNamaBank">NAMA BANK</label>
                    <FormInput onChange={this.handleChangeNAMA_BANK} value={this.state.NAMA_BANK}  id="feNamaBank" />
                  </Col>
                </Row>

                <FormGroup>
                  <label htmlFor="feJumlahRekening">JUMLAH REKENING</label>
                  <FormInput onChange={this.handleChangeJUMLAH_REKENING} value={this.state.JUMLAH_REKENING}  id="feJumlahRekening" type="number" />
                </FormGroup>

                <FormGroup>
                  <label htmlFor="feKodeBank">KODE BANK</label>
                  <FormInput onChange={this.handleChangeKODE_BANK} value={this.state.KODE_BANK}  id="feKodeBank" />
                </FormGroup>

                <FormGroup>
                  <label htmlFor="feStatus">STATUS</label>
                  <FormSelect value={this.state.STATUS} onChange={this.handleChangeSTATUS}  id="feStatus">
                    <option value="AKTIF">AKTIF</option>
                    <option value="TIDAK AKTIF">TIDAK AKTIF</option>
                  </FormSelect>
                </FormGroup>
                <Button onClick={async () => await this.submit()}>SUBMIT DATA</Button>
              </Form>
            </Col>
          </Row>
        </ListGroupItem>
      </ListGroup>
    );
  }
}

export default CompleteFormExampleMasterBank;
